<script>
import $ from 'webpack-zepto'
export default{
  name: 'userAPI',
  methods: {
    getUser: function (id, cb) {
      $.ajax({
        dataType: 'json',
        type: 'post',
        url: '/api/user.php?action=getUser',
        data: {
          Id: id
        },
        success: function (data) {
          cb(null, data)
        },
        error: () => {
          cb('error', null)
        }
      })
    },
    // 当个人信息不为空时，执行回调
    valiUser: function (cb) {
      this.getMine((err, data) => {
        if (err || data == null) {
          window.alert('网络错误，请稍后再试。')
        } else if (data.phone == null || data.position == null) {
          window.alert('请先完善个人信息呦~')
          this.$router.push('/profile')
        } else {
          cb()
        }
      })
    },
    getMine: function (cb) {
      $.ajax({
        type: 'GET',
        url: '/api/user.php?action=getMine',
        dataType: 'json',
        success: function (data) {
          cb(null, data)
        },
        error: function () {
          cb('err', null)
        }
      })
    }
  }
}
</script>



