<template>
  <div id="newOrder">
    <Loading
    :show = loadingFlag></Loading>
    <Toast
    :show = "toast.flag"
    :type = "toast.type"></Toast>
    <div class="weui-cells__title">表单</div>
<div class="weui-cells weui-cells_form">
    <div class="weui-cell weui-cell_vcode">
        <div class="weui-cell__hd">
            <label class="weui-label">价格</label>
        </div>
        <div class="weui-cell__bd">
            <input class="weui-input" v-model="price" type="number" placeholder="请输入赏金">
        </div>
        <div class="weui-cell__ft">
            <a href="javascript:;" class="weui-vcode-btn">RMB</a>
        </div>
    </div>
</div>
    <div class="weui-cells__title">物件大小</div>
<div class="weui-cells weui-cells_radio">
    <label class="weui-cell weui-check__label" for="x11">
        <div class="weui-cell__bd">
            <p>小</p>
        </div>
        <div class="weui-cell__ft">
            <input type="radio" class="weui-check" name="size" value="1" v-model="size" id="x11">
            <span class="weui-icon-checked"></span>
        </div>
    </label>
    <label class="weui-cell weui-check__label" for="x12">

        <div class="weui-cell__bd">
            <p>中</p>
        </div>
        <div class="weui-cell__ft">
            <input type="radio" name="size" value="2" class="weui-check"v-model="size" id="x12">
            <span class="weui-icon-checked"></span>
        </div>
    </label>
     <label class="weui-cell weui-check__label" for="x13">

        <div class="weui-cell__bd">
            <p>大</p>
        </div>
        <div class="weui-cell__ft">
            <input type="radio" name="size" value="3" v-model="size" class="weui-check" id="x13">
            <span class="weui-icon-checked"></span>
        </div>
    </label>
</div>
<div class="weui-cells__title">快递信</div>
<div class="weui-cells weui-cells_form">
    <div class="weui-cell">
        <div class="weui-cell__bd">
            <textarea class="weui-textarea" placeholder="请复制你的快递信" rows="3" v-model="sms"></textarea>
            <div class="weui-textarea-counter"><span >{{sms.length}}</span>/200</div>
        </div>
    </div>
</div>
<!--文本域-->
<div class="weui-cells__title">备注</div>
<div class="weui-cells weui-cells_form">
    <div class="weui-cell">
        <div class="weui-cell__bd">
            <textarea class="weui-textarea" placeholder="请留下您方便的收货时间" rows="2" v-model="remark"></textarea>
            <div class="weui-textarea-counter"><span>{{remark.length}}</span>/50</div>
        </div>
    </div>
</div>
<div class="button">
  <a href="javascript:;" @click=newOrder() class="weui-btn weui-btn_primary">提交</a>
<router-link to="/index" href="javascript:;"  class="weui-btn weui-btn_warn">取消</router-link>
</div>
  </div>
</template>
<script>
import util from 'components/mixins/util.vue'
import payApi from 'components/mixins/api/pay.vue'
import userApi from 'components/mixins/api/user.vue'
import $ from 'webpack-zepto'
import Loading from 'components/loading'
import Toast from 'components/toast'
export default {
  mixins: [util, payApi, userApi],
  name: 'Order',
  data () {
    return {
      toast: {
        flag: false,
        type: 'error'
      },
      sms: '',
      loadingFlag: false,
      price: '',
      size: '',
      remark: '',
      receiveTime: '', // 这个是不是不需要
      userID: ''
    }
  },
  created: function () {
    this.valiUser(() => {
    })
  },
  methods: {
    getTime: function () {
      let myDate = new Date()
      return myDate.getFullYear() + '.' + (myDate.getMonth() + 1) + '.' + myDate.getDate()
    },
    newOrder: function () {
      this.getMine((err, data) => {
        if (err) {
          window.alert('网络错误')
          this.$router.push('/index')
        } else if (data.phone == null || data.position == null) {
          window.alert('请先完善个人信息！')
          this.$router.push('/profile')
          return
        } else {
          var that = this
          // TODO 这里有些字段没有添加
          // TODO 可以把这些都放到一个文件中吗
          let postData = {
            expire: '3600',
            '快递信': that.sms,
            '快递大小': that.size
          }
          let t = this.vali(postData)
          let tt = this.vali(that.price, 'price')
          if (!t || !tt) return
          this.loadingFlag = true
          $.ajax({
            type: 'POST',
            url: '/api/order.php?action=new',
            data: {
              date: that.getTime(),
              price: that.price,
              expire: '3600',
              size: that.size,
              remark: that.remark === '' ? '未填写' : that.remark,
              receiveTime: '1477836244',
              SMS: that.sms
            },
            success: function (data) {
              that.loadingFlag = false
              /* eslint-disable */
              eval(data)
              /* eslint-enable */
            },
            error: function () {
              that.loadingFlag = false
              that.showToast('error')
            }
          })
        }
      })
    },
    showToast (type) {
      this.toast.flag = true
      this.toast.type = type
      let that = this
      setTimeout(() => {
        that.toast.flag = false
      }, 2000)
    }
  },
  components: {
    Loading, Toast
  }
}
</script>


<style scoped lang="scss">
.button{
  margin:20px;
  margin-top:90px;
}
</style>



